.mobile_nav {
  position: absolute;
  top: 14%;
  left: 50%;
  z-index: 10;
  transform: translateX(-50%);
  width: 90%;
}
.mobile_nav__box {
  max-width: 37.5rem;
  margin: 0 auto;
  width: 100%;
  background-color: #fff;
  padding: 3rem 0;
  border-radius: 3px;
}
.mobile_nav__list {
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
  list-style: none;
  text-align: center;
}
.mobile_nav a:link,
.mobile_nav a:visited {
  text-decoration: none;
  color: hsl(233deg, 12%, 13%);
  font-size: 1.6rem;
  font-weight: 700;
}